<template>
	<div class="activity">
		<div class="download">
			<img src="../images/activity.png">
			<b>新一代来者不拒的借款神器</b>
			<img src="../images/activity_download.png" @click="openApp">
		</div>
		<div class="phone">
			<input type="number" v-model="phone" placeholder="请输入手机号码">
		</div>
		<div class="password">
			<input type="password" v-model="password" placeholder="请输入6-16位字符密码">
		</div>
		<div class="sms">
			<input type="number" v-model="smsCode" placeholder="请输入验证码">
			<span @click="send">
				<timer-btn ref="btn" :second="60"></timer-btn>
			</span>
			<div v-show='boxType' class="forgetBox" :state='boxState' @click='box'></div>
		</div>
		<div class="btn">
			<img src="../images/activity_btn.png" @click="download">
		</div>
		<div class="bottomText">
			<div class="left">
				<span class="checkbox" @click="pols">
					<img src="../images/activity_checkActive.png" v-show="pol">
				</span>
				<label>同意《使用协议》</label>
			</div>
		</div>
		<div class="beian">
			<p>厦门灏义金融服务有限公司</p>
			闽ICP备17032482号
		</div>
	</div>
</template>
<script>
let Base64 = require('js-base64').Base64
import { strEnc } from '../common/js/des.js'
import { fetchSmsVCode, verifySms, loginRegister } from '../api/api'
import { Toast } from 'mint-ui'
export default {
	data() {
		return {
			phone: '', // 手机电话
			boxType: true, // 短信验证码状态
			smsCode: '', // 验证码
			password: '', // 密码
			invitationCode: '', // 邀请码
			client: 'wx', // 客户端类型
			registerAddr: '', // 注册地址
			channelCode: '', // 渠道编码
			registerCoordinate: '', // 注册坐标，经纬度
			pol: true
		}
	},
	methods: {
		pols() {
			if (this.pol === true) {
				this.pol = false
			} else {
				this.pol = true
			}
		},
		box() {
			var phoneReg = /^1[3|4|5|7|8][0-9]{9}$/
			if (phoneReg.test(this.phone)) {
    		} else {
    			Toast({
					message: '请输入正确手机号码',
					position: 'bottom',
					duration: 2000
				});
    		}
		},
		openApp() {
			// window.location = 'com.jiejired.qyq://'
            // setTimeout(function() {
            // 	window.location = 'https://itunes.apple.com/cn/app/id1324980243?mt=8'
            // }, 25)
            // var app = navigator.appVersion
            var u = navigator.userAgent
            var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1
            var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)
            if (isAndroid === true) {
            	window.location = 'http://sj.qq.com/myapp/detail.htm?apkName=com.minhua.jijiehao2'
            } else if (isiOS === true) {
            	window.location = 'https://itunes.apple.com/cn/app/id1324980243?mt=8'
            }
		},
		send() {
			var phoneReg = /^1[3|4|5|7|8][0-9]{9}$/
			if (phoneReg.test(this.phone)) {
				let params = {
					phone: this.phone,
					type: 'REGISTER'
				}
				fetchSmsVCode(params).then(res => {
					if (res.data.state === '10') {
						Toast({
							message: res.msg,
							position: 'bottom',
							duration: 2000
						});
					} else if (res.data.state === '20') {
						Toast({
							message: res.data.message,
							position: 'bottom',
							duration: 2000
						});
					} else if (res.code === 500) {
						Toast({
							message: res.msg,
							position: 'bottom',
							duration: 2000
						});
					}
				})
			} else {
				Toast({
					message: '请输入正确手机号码',
					position: 'bottom',
					duration: 2000
				});
			}
		},
		download() {
			var phoneReg = /^1[3|4|5|7|8][0-9]{9}$/
			var passReg = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,16}$/
			if (phoneReg.test(this.phone)) {
				if (this.pol === true) {
					if (this.smsCode === '' || this.smsCode.length !== 4) {
						Toast({
							message: '请输入正确的验证码',
							position: 'bottom',
							duration: 2000
						});
					} else {
						let params = {
							phone: this.phone,
							type: 'REGISTER',
							vcode: this.smsCode
						}
						verifySms(params).then(res => {
							if (res.data.state === '10') {
								var key = 'pasec23der1e12ecjer3AFRGYD03847@@*^DEJUsdu33'
								var aa = strEnc(this.password, key)
								var loginWord = Base64.encode(aa)
								if (passReg.test(this.password)) {
									let params = {
										loginName: this.phone,
										loginPwd: loginWord,
										vcode: this.smsCode,
										invitationCode: this.invitationCode,
										client: this.client,
										registerAddr: this.registerAddr,
										channelCode: this.channelCode,
										registerCoordinate: this.registerCoordinate,
										signMsg: '',
										blackBox: '',
										mobileType: '3'
									}
									loginRegister(params).then(res => {
										if (res.code === 200) {
											var u = navigator.userAgent
								            var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1
								            var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)
								            if (isAndroid === true) {
								            	// window.location = 'http://m.51jjhao.com/act/downloadApk.htm'
								            	window.location = 'http://sj.qq.com/myapp/detail.htm?apkName=com.minhua.jijiehao2'
								            } else if (isiOS === true) {
								            	window.location = 'https://itunes.apple.com/cn/app/id1324980243?mt=8'
								            }
										} else if (res.code !== 200) {
											Toast({
												message: res.msg,
												position: 'bottom',
												duration: 2000
											});
										}
									})
								} else {
									Toast({
										message: '密码为6-16位字符，包括数字和字母',
										position: 'bottom',
										duration: 2000
									});
								}
							} else if (res.data.state === '20') {
								Toast({
									message: res.data.message,
									position: 'bottom',
									duration: 2000
								});
							}
						})
					}
				} else {
					Toast({
						message: '请同意使用协议',
						position: 'bottom',
						duration: 2000
					})
				}
			} else {
				Toast({
					message: '请输入正确手机号码',
					position: 'bottom',
					duration: 2000
				})
			}
		}
	},
	computed: {
		boxState() {
			var phoneReg = /^1[3|4|5|7|8][0-9]{9}$/
  			if (phoneReg.test(this.phone)) {
  				this.boxType = false
  			} else {
  				this.boxType = true
  			}			
  		}
	},
	components: {
		'timer-btn': require('../components/PlugInUnit/timerbtn.vue')
	}
}
</script>
<style lang="scss" rel="stylesheet/scss">
	.activity {
		height: 100%;
		z-index: 11;
		background: url(./../images/H5_bg.png) no-repeat center;
		background-size: 100% 100%;
		position: relative;
		.download {
			width: 100%;
			height: 0.65rem;
			position: absolute;
			top: 0;
			left: 0;
			background: rgba(0,0,0,0.7);
			padding: 0.22rem;
			padding-top: 0.05rem;
			box-sizing: border-box;
			img {
				vertical-align: middle;
			}
			img:nth-child(1) {
				width: 0.5rem;
				position: absolute;
				top: 50%;
				left: 0.22rem;
				transform: translateY(-50%);
			}
			img:nth-child(3) {
				width: 1rem;
				position: absolute;
				top: 50%;
				right: 0.22rem;
				transform: translateY(-50%);
			}
			b {
				color: #fff;
				font-size: 0.22rem;
				position: absolute;
				top: 50%;
				left: 0.92rem;
				transform: translateY(-50%);
			}
		}
		.phone {
			width: 100%;
			text-align: center;
			// position: absolute;
			// bottom: 4.4rem;
			margin-top: 6rem;
			input {
				width: 5rem;
				height: 1rem;
				border: 0.05rem solid #7dcaff;
				border-radius: 0.2rem;
				padding: 0 0.2rem;
				box-sizing: border-box; 
				outline: none;
			}
		}
		.password {
			width: 100%;
			text-align: center;
			// position: absolute;
			// bottom: 3.3rem;
			margin-top: 0.2rem;
			input {
				width: 5rem;
				height: 1rem;
				border: 0.05rem solid #7dcaff;
				border-radius: 0.2rem;
				padding: 0 0.2rem;
				box-sizing: border-box; 
				outline: none;
			}
		}
		.sms {
			width: 100%;
			text-align: center;
			// position: absolute;
			margin-top: 0.2rem;
			position: relative;
			// bottom: 2.2rem;
			input {
				width: 5rem;
				height: 1rem;
				border: 0.05rem solid #7dcaff;
				border-radius: 0.2rem;
				padding: 0 0.2rem;
				box-sizing: border-box; 
				outline: none;
			}
			button {
				position: absolute;
				top: 50%;
				right: 1.5rem;
				border: 0;
				-webkit-transform: translateY(-50%);
				-moz-transform: translateY(-50%);
				-ms-transform: translateY(-50%);
				background: transparent;
				font-size: 0.22rem;
				letter-spacing: 0.022rem;
				color: #7dcaff;
			}
			.forgetBox {
				position: absolute;
				top: 50%;
				right: 1.5rem;
				bottom: 0;
				width: 1.8rem;
			}
		}
		.btn {
			width: 100%;
			text-align: center;
			// position: absolute;
			// bottom: 1.2rem;
			margin-top: 0.2rem;
			img {
				width: 5rem;
			}
		}
		.bottomText {
			width: 100%;
			// position: absolute;
			// bottom: 0.9rem;
			margin-top: 0.1rem;
			.left {
				width: 5rem;
				margin: 0 auto;
				color: #d9d9d9;
				.checkbox {
					width: 0.2rem;
					height: 0.2rem;
					background: url(./../images/activity_checkbox.png) no-repeat center;
					background-size: cover;
					position: relative;
					display: inline-block;
					vertical-align: bottom;
					img {
						width: 0.23rem;
						position: absolute;
						left: 0;
						bottom: 0;
					}
				}
			}
		}
		.beian {
			width: 100%;
			text-align: center;
			// position: absolute;
			// bottom: 0.4rem;
			margin-top: 0.1rem;
			font-size: 0.22rem;
			line-height: 0.2rem;
			color: #d9d9d9;
			letter-spacing: 0.022rem;
			p {
				margin-bottom: 0.05rem;
			}
		}
	}
</style>



